@import url(https://fonts.googleapis.com/css?family=Montserrat:200,400,700&display=swap);
body {
  font-family: 'Montserrat', sans-serif;

  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.App {
  min-height: 100vh;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: -webkit-gradient(linear, left top, right top, from(#c9d6ff), to(#e2e2e2));
  background: linear-gradient(to right, #c9d6ff, #e2e2e2); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-size: cover;
}

.Nav {
  background-color: rgb(65, 65, 65);
  padding: 0.3em;
  text-transform: uppercase;
  text-align: left;
}

.Nav-link {
  font-size: 0.9em;
  padding: 0 1em;
  color: rgb(192, 192, 192);
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}

.Nav-link:hover {
  color: rgb(255, 208, 0);
}

.Nav-active {
  color: white;
}

a.Nav-link {
  text-decoration: none;
}

.Nav-home {
  color: rgb(255, 208, 0);
  font-weight: bold;
  font-size: 1.2em;
}

.Dog {
  margin: 2em 0 auto 0;
  align-self: center;
  width: 40em;
  max-width: 90%;
  max-height: 90%;
}

.Dog-inner {
  display: flex;
  flex-direction: column;
  background-color: rgb(250, 253, 255);
  box-shadow: 10px 10px 50px -15px rgba(0, 0, 0, 0.2);
  margin-bottom: 1.5em;
}

.Dog-img {
  height: 40em;
  width: 100%;
  object-fit: cover;
}

.Dog-description {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.Dog-description-left {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.Dog-name {
  margin-bottom: 0;
  padding-left: 0;
  font-weight: 200;
}
.Dog-age {
  margin-top: 0;
  padding-left: 0;
}

.Dog-facts {
  text-align: left;
  list-style-type: none;
  padding: 0;
}

.Dog-fact {
  padding: 0.3em 0;
  border-bottom: 1px dashed grey;
}

.Dog-button {
  padding: 1em;
  background: rgb(255, 208, 0);
  font-family: inherit;
  border: none;
  outline: none;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
  -webkit-transition: all 0.05s linear;
  transition: all 0.05s linear;
}

.Dog-button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

@media (max-width: 576px) {
  .Dog-img {
    max-height: 60vh;
  }
  .Dog-description {
    flex-direction: column;
  }
  .Dog-description-left {
    flex-direction: row;
  }
  .Dog-name {
    margin: 0.7rem 1rem 0 1rem;
    padding: 0.7rem 1rem 0 1rem;
    display: inline-block;
  }
  .Dog-age {
    margin: 0.7rem 1rem 0 1rem;
    padding: 0.7rem 1rem 0 1rem;
    display: inline-block;
  }
  .Dog-button {
    margin: 1rem 0.5rem 1.5rem 0.5rem;
  }
}

.Dogs {
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: space-between;
}

.Dogs-dogs {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  flex-wrap: wrap;
}

.Dogs-img {
  margin-top: 1em;
  max-height: 30em;
  max-width: 85%;
  box-shadow: 10px 10px 50px -15px rgba(0, 0, 0, 0.2);
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
  -webkit-filter: grayscale();
          filter: grayscale();
}

.Dogs-dog:hover .Dogs-img {
  -webkit-filter: none;
          filter: none;
  -webkit-transform: scale(1.01);
          transform: scale(1.01);
}

.Dogs-title {
  color: rgb(88, 88, 88);
  font-weight: 200;
  font-size: 5em;
  margin: 0;
  padding: 0.2em 0;
  border-bottom: 3px solid rgb(255, 208, 0);
}

.Dogs a {
  text-decoration: none;
}

.Dogs-dog:hover a {
  /* text-decoration: underline; */
}

.Dogs-dogname {
  margin-top: 2em;
  color: rgb(65, 65, 65);
  letter-spacing: 0.2em;
  border-bottom: 2px solid transparent;
  -webkit-transition: all 0.5s ease;
  transition: all 0.5s ease;
}

.Dogs-dog:hover .Dogs-dogname {
  color: rgb(65, 65, 65);
  letter-spacing: 0.2em;
  border-bottom: 2px solid rgb(255, 208, 0);
}

